@import "../_cms-mixins";
@import "../_cms-variables";

.@{container-class}{

	background: @container-bg-color;
	overflow: hidden;
	i,em{
		font-style: normal;
	}
	.mt2{
		margin-top: 2%;
	}
	.item-v-list{
		.item{
			padding-top:50%;
			padding-bottom: 3.5rem;
		}
	}
	.unable-sales{
		position: relative;

		&:after{
			content:"";
			.absolute(0,_,_,0);
			.size(100%);
			background: data-uri('@{cms-image-dir}/qiangguangguang.png') center no-repeat;
			background-size: 80% 80%;
		}
	}
	.prolist-title,.load-title{
		line-height:50px;
		color: #999;
		text-align: center;
		position: relative;

		&:before{
			content:"";
			height:1px;
			.absolute(50%,_,_,50%);
		}
		span{
			vertical-align: top;
			display: inline-block;
			padding: 0 4%;
			background: #eee;
			position: relative;
		}

	}
	.prolist-title:before{
		width:80%;
		margin-left: -40%;
		background: #999;
	}
	.load-title:before{
		width:90%;
		margin-left: -45%;
		background: #d5d5d5;
	}
}
