@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-level-list(){

	.item{
		padding: 10px 0;
		border-bottom: 1px solid @container-bg-color;

		&:last-child{
			border-bottom: none;

			.line-fix{
				display: none;
			}
		}
	}

	.pic{
		padding-right: 10px;
		position: relative;
	}

	.line-fix{
		.absolute(_,_,-11px,0);
		.size(100%, 1px);
		background: @body-background;
	}

	.info{
		padding-right: 10px;
	}

	.title{
		display: block;
		padding-top: 5px;
		.ellipsis(2);
	}


	.sub-price{
		color:#999;
		padding-top: 5px;
		font-size: @font-size-sm;
		.ellipsis();
	}

	.sub{
		color: @ht-color;
		padding-top: 5px;
		font-size: @font-size-sm;
	}

	.min-tit{
		color: #ff8b26;
		padding-top: 5px;
		font-size: @font-size-sm;
		.ellipsis();
	}

	.detail{
		width: 100%;
		padding-top: 2%;
		overflow: hidden;
		.absolute(_,_,5px,_);
		display: flex;
    	justify-content: space-between;

		del{
			color: #999;
			font-size: @font-size-sm;
			 padding-left: 5px;
		}

		.vip{
			border: 1px #e60012 solid;
		    color: #e60012;
		    font-size: @font-size-sm;
		    position: relative;
		    top: -1px;
		    left: 0;
		    margin-left: 5px;
		    border-radius: 2px;
		}

		.ico{
			color: #999;
		    font-size: @font-size-lg;
		    padding-right: 5%;
		    display: block;

			i{
				color: @ht-color;
				font-size: unit(@font-size-lg, rem);
			}
			
			.text{
				margin-top: 3px;
				font-size: @font-size-sm;
			    font-family: arial, 'Microsoft Yahei';
			    .ellipsis();
			    display: inline-block;
			}
		}
	}
}